footer {
  display: flex;
  width: 100%;
  height: 154px;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  background-color: $primarycolor;
  p {
    color: $fontcolor;
    text-align: center;
    font-family: Poppins;
    font-size: 16px;
    font-style: italic;
    font-weight: 400;
    line-height: normal;
    letter-spacing: 3.2px;
  }
}
